import { hillo, IKDataEntity } from 'innerken-utils'
import Dish from './Dish'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    formConfig: {
      requiredNew: false,
    },
  },
  childDishId: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      required: false,
      type: {
        selectItems: Dish.getBuffet,
        itemText: '_langsname',
      },
    },
  },
  adultDishId: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      required: false,
      type: {
        selectItems: Dish.getBuffet,
        itemText: '_langsname',
      },
    },
  },
}
const config = {
  add: function (item) {
    return hillo.postWithUploadFile('Dishes.php?op=addJBuffetChild', item, {
      showLoading: true,
    })
  },
  edit: function (item) {
    return hillo.postWithUploadFile('Dishes.php?op=editJBuffetChild', item, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post('Dishes.php?op=deleteJBuffetChild', { id: id })
  },
  load: async function (filter) {
    return (await hillo.get('Dishes.php?op=showJBuffetChild', {
      ...filter,
    })).content.map(function (item) {
      return IKDataEntity.parseDataForEntity(item, entity)
    })
  },
}

export default IKDataEntity.ModelFactory(entity, config)
